"use client";

import { ReactNode, createContext, useContext, useEffect, useState } from "react";

interface CompareContextType {
  selectedIds: number[];
  toggleId: (id: number) => void;
  clear: () => void;
}

const CompareContext = createContext<CompareContextType | undefined>(undefined);

export function CompareProvider({ children }: { children: ReactNode }) {
  const [selectedIds, setSelectedIds] = useState<number[]>([]);

  // ì´ˆê¸° ë¡œë“œ ì‹œ ë¡œì»¬ ìŠ¤í† ë¦¬ì§€ì—ì„œ ë¶ˆëŸ¬ì˜¤ê¸° (ìƒˆë¡œê³ ì¹¨ í•´ë„ ìœ ì§€ë˜ë„ë¡)
  useEffect(() => {
    const saved = localStorage.getItem("compareIds");
    if (saved) setSelectedIds(JSON.parse(saved));
  }, []);

  // ë³€ê²½ë  ë•Œë§ˆë‹¤ ë¡œì»¬ ìŠ¤í† ë¦¬ì§€ì— ì €ì¥
  useEffect(() => {
    localStorage.setItem("compareIds", JSON.stringify(selectedIds));
  }, [selectedIds]);

  const toggleId = (id: number) => {
    setSelectedIds((prev) => {
      if (prev.includes(id)) return prev.filter((i) => i !== id); // ì´ë¯¸ ìˆìœ¼ë©´ ì œê±°
      if (prev.length >= 3) {
        alert("ë¹„êµëŠ” ìµœëŒ€ 3ê°œê¹Œì§€ë§Œ ê°€ëŠ¥í•©ë‹ˆë‹¤."); // ìµœëŒ€ 3ê°œ ì œí•œ
        return prev;
      }
      return [...prev, id]; // ì—†ìœ¼ë©´ ì¶”ê°€
    });
  };

  const clear = () => setSelectedIds([]);

  return (
    <CompareContext.Provider value={{ selectedIds, toggleId, clear }}>
      {children}
    </CompareContext.Provider>
  );
}

export function useCompare() {
  const context = useContext(CompareContext);
  if (!context) throw new Error("useCompare must be used within a CompareProvider");
  return context;
}